.home {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  .item {
    width: calc(50% - 40px);
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px;

    img {
      width: 70%;
    }
    box-shadow: 2px 2px 10px 4px #b9b3b3;
  }

  .ant-picker-calendar-full .ant-picker-panel .ant-picker-calendar-date-content {
    height: auto;
  }
}
