.login {
  width: 100vw;
  height: 100vh;
  background: var(--gary) url("../../images/anti-virus.png") no-repeat;
  display: flex;
  align-items: center;
  background-size: cover;
  justify-content: center;

  .form {
    background-color: var(--white);
    padding: 40px 100px 20px;
    border-radius: 4px;
    box-shadow: 0 1px 20px 0 rgba(0, 0, 0, 0.16);
  }

  h2 {
    color: #6f6d6d;
    font-weight: bolder;
    padding-bottom: 15px;
  }
}
