.app {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  .content {
    width: 100%;
    height: calc(100vh - var(--header-info-height));
    padding: 10px 20px;
    overflow: auto;
  }

  img {
    width: 100%;
    height: auto;
  }

  a {
    text-decoration: none;
    color: inherit;
  }
}


