.bl-header {
  background-color: var(--light-blue);
  color: var(--white);
  display: flex;
  justify-content: space-between;
  height: var(--header-info-height);
  align-items: center;

  .title {
    font-size: var(--font-l);
    font-weight: var(--bolder);
    padding-left: 20px;
  }


  .rightInfo {
    display: flex;
    justify-content: flex-end;
  }
  .item {
    display: block;
    margin: 10px;
    cursor: pointer;
  }
}
